import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
    Box,
    Typography,
    useTheme,
    useMediaQuery,
    TextField,
    Button,
    Alert,
    Collapse,
    Card,
} from "@mui/material";

const GrammarCorrection = () => {
    const theme = useTheme();
    // Media
    const isNotMobile = useMediaQuery("(min-width: 1000px)");
    // States
    const [text, setText] = useState("");
    const [correctedText, setCorrectedText] = useState("");
    const [error, setError] = useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post("/api/v1/openai/grammerCorrection", {
                text,
            });
            console.log(data);
            setCorrectedText(data);
        } catch (err) {
            console.log(err);
            setError("Grammar correction failed.");
            setTimeout(() => {
                setError("");
            }, 5000);
        }
    };

    return (
        <Box
            width={isNotMobile ? "40%" : "80%"}
            p={"2rem"}
            m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            <Collapse in={!!error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>
            <form onSubmit={handleSubmit}>
                <Typography variant="h3">Grammar Correction</Typography>

                <TextField
                    placeholder="Enter text with possible grammar errors"
                    type="text"
                    multiline
                    required
                    margin="normal"
                    fullWidth
                    value={text}
                    onChange={(e) => {
                        setText(e.target.value);
                    }}
                />

                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    sx={{ color: "white", mt: 2 }}
                >
                    Correct Grammar
                </Button>
                <Typography mt={2}>
                    Not the tool you're looking for? <Link to="/">Go back</Link>
                </Typography>
            </form>

            {correctedText ? (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                        overflow: "auto",
                        padding: 2,
                    }}
                >
                    <Typography>{correctedText}</Typography>
                </Card>
            ) : (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                    }}
                >
                    <Typography variant="h5" color="natural.main">
                        The corrected text will appear here.
                    </Typography>
                </Card>
            )}
        </Box>
    );
};

export default GrammarCorrection;
