import React from "react";
import { Box, Typography, Card, Stack } from "@mui/material";
import { useNavigate } from "react-router-dom";
import DescriptionRounded from "@mui/icons-material/DescriptionRounded";
import FormatAlignLeftOutlined from "@mui/icons-material/FormatAlignLeftOutlined";
import ChatRounded from "@mui/icons-material/ChatRounded";
import AddPhotoAlternateIcon from '@mui/icons-material/AddPhotoAlternate';
import JavascriptIcon from '@mui/icons-material/Javascript';
import CodeIcon from '@mui/icons-material/Code';
import TranslateIcon from '@mui/icons-material/Translate';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import CheckIcon from '@mui/icons-material/Check';
import FindInPageIcon from '@mui/icons-material/FindInPage';

const Homepage = () => {
    const navigate = useNavigate();
    return (
        <>
            <Box sx={{ display: "flex", flexDirection: "row" }} marginTop={-3} marginLeft={6}>
                <Box p={5}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Summerizer
                    </Typography>
                    <Card
                        onClick={() => navigate("/summary")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <DescriptionRounded
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                TEXT SUMAMRY
                            </Typography>
                            <Typography variant="h6">
                                Summarize long text into short sentences
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Parapgraph Generation
                    </Typography>
                    <Card
                        onClick={() => navigate("/paragraph")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <FormatAlignLeftOutlined
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Parapgraph
                            </Typography>
                            <Typography variant="h6">
                                Generate Paragraph with words
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        AI ChatBot
                    </Typography>
                    <Card
                        onClick={() => navigate("/chatbot")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <ChatRounded
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Chatbot
                            </Typography>
                            <Typography variant="h6">Chat With AI Chatbot</Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Javascript Converter
                    </Typography>
                    <Card
                        onClick={() => navigate("/js-converter")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <JavascriptIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                JS CONVERTER
                            </Typography>
                            <Typography variant="h6">
                                Enter statement and get your Js Code.
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5}>
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        AI SCIFI Images
                    </Typography>
                    <Card
                        onClick={() => navigate("/scifi-image")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <AddPhotoAlternateIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Scifi Image
                            </Typography>
                            <Typography variant="h6" >Generate Scifi images</Typography>
                        </Stack>
                    </Card>
                </Box>
            </Box>
            <Box sx={{ display: "flex", flexDirection: "row" }} marginTop={-3} marginLeft={6}>
                <Box p={5} >
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        C++ Generator
                    </Typography>
                    <Card
                        onClick={() => navigate("/cppConverter")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <CodeIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Generate C++ Code
                            </Typography>
                            <Typography variant="h6">
                                Convert your sentences into C++ Codes.
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5} >
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Marathi Translator
                    </Typography>
                    <Card
                        onClick={() => navigate("/EngtoMarathi")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <TranslateIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Convert to Marathi
                            </Typography>
                            <Typography variant="h6">
                                Get conversion of any sentence in Marathi.
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5} >
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Sentiment Analyser
                    </Typography>
                    <Card
                        onClick={() => navigate("/SentimentAnalysis")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <AnalyticsIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Sentiment Analysis
                            </Typography>
                            <Typography variant="h6">Send a sentence and it will provide the Sentiments behind it.</Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5} >
                    <Typography variant="h4" mb={2} fontWeight="bold">
                        Grammer Correction
                    </Typography>
                    <Card
                        onClick={() => navigate("/grammerCorrection")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <CheckIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                Correct you Grammer
                            </Typography>
                            <Typography variant="h6">
                                Enter text and get grammatical error free text.
                            </Typography>
                        </Stack>
                    </Card>
                </Box>
                <Box p={5} >
                    <Typography variant="h4" mb={2} fontWeight="bold" >
                        Named Entity Recognition
                    </Typography>
                    <Card
                        onClick={() => navigate("/ner")}
                        sx={{
                            boxShadow: 2,
                            borderRadius: 5,
                            height: 190,
                            width: 200,
                            "&:hover": {
                                border: 2,
                                boxShadow: 0,
                                borderColor: "primary.dark",
                                cursor: "pointer",
                            },
                        }}
                    >
                        <FindInPageIcon
                            sx={{ fontSize: 80, color: "primary.main", mt: 2, ml: 2 }}
                        />
                        <Stack p={3} pt={0}>
                            <Typography fontWeight="bold" variant="h5">
                                NER
                            </Typography>
                            <Typography variant="h6" >A tool that identifies and extracts named entities from Texts.</Typography>
                        </Stack>
                    </Card>
                </Box>
            </Box>
            <div style={{ position: 'fixed', bottom: 20, textAlign: 'center', width: '100%' }}>
                <Typography variant="h5" style={{ color: 'red' }}>
                    As OpenAI API is NOT FREE these tools are not working right now.
                </Typography>
            </div>
        </>
    );
};

export default Homepage;