import React, { useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
    Box,
    Typography,
    useTheme,
    useMediaQuery,
    TextField,
    Button,
    Alert,
    Collapse,
    Card,
} from "@mui/material";

const NERTool = () => {
    const theme = useTheme();
    //media
    const isNotMobile = useMediaQuery("(min-width: 1000px)");
    // states
    const [text, setText] = useState("");
    const [entities, setEntities] = useState([]);
    const [error, setError] = useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post("/api/v1/openai/ner", {
                text,
            });
            console.log(data);
            setEntities(data.entities);
        } catch (err) {
            console.log(err);
            setError("NER failed.");
            setTimeout(() => {
                setError("");
            }, 5000);
        }
    };

    return (
        <Box
            width={isNotMobile ? "40%" : "80%"}
            p={"2rem"}
            m={"2rem auto"}
            borderRadius={5}
            sx={{ boxShadow: 5 }}
            backgroundColor={theme.palette.background.alt}
        >
            <Collapse in={!!error}>
                <Alert severity="error" sx={{ mb: 2 }}>
                    {error}
                </Alert>
            </Collapse>
            <form onSubmit={handleSubmit}>
                <Typography variant="h3">Named Entity Recognition</Typography>

                <TextField
                    placeholder="Enter your text"
                    type="text"
                    multiline
                    required
                    margin="normal"
                    fullWidth
                    value={text}
                    onChange={(e) => {
                        setText(e.target.value);
                    }}
                />

                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    sx={{ color: "white", mt: 2 }}
                >
                    Recognize Entities
                </Button>
                <Typography mt={2}>
                    Not the tool you're looking for? <Link to="/">Go back</Link>
                </Typography>
            </form>

            {entities.length > 0 ? (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                        overflow: "auto",
                    }}
                >
                    <pre>
                        {entities.map((entity, index) => (
                            <Typography p={2} key={index}>
                                {`${entity.label}: ${entity.text}`}
                            </Typography>
                        ))}
                    </pre>
                </Card>
            ) : (
                <Card
                    sx={{
                        mt: 4,
                        border: 1,
                        boxShadow: 0,
                        height: "500px",
                        borderRadius: 5,
                        borderColor: "natural.medium",
                        bgcolor: "background.default",
                    }}
                >
                    <Typography
                        variant="h5"
                        color="natural.main"
                        sx={{
                            textAlign: "center",
                            verticalAlign: "middle",
                            lineHeight: "450px",
                        }}
                    >
                        No entities found
                    </Typography>
                </Card>
            )}
        </Box>
    );
};

export default NERTool;
